// src/features/auth/auth-context.jsx
"use client"

import * as React from "react"

const AuthContext = React.createContext(null)

export function AuthProvider({ children }) {
  const [user, setUser] = React.useState(null)
  const [isLoading, setIsLoading] = React.useState(true)
  const [isAuthenticating, setIsAuthenticating] = React.useState(false)

  const fetchSession = React.useCallback(async () => {
    setIsLoading(true)
    try {
      const response = await fetch("/api/auth/openid", {
        method: "GET",
        credentials: "include",
      })
      if (!response.ok) {
        throw new Error(`Failed to load session: ${response.status}`)
      }
      const payload = await response.json()
      setUser(payload?.user ?? null)
    } catch (error) {
      console.warn("Unable to fetch auth session", error)
      setUser(null)
    } finally {
      setIsLoading(false)
    }
  }, [])

  React.useEffect(() => {
    fetchSession()
  }, [fetchSession])

  const login = React.useCallback(async () => {
    setIsAuthenticating(true)
    try {
      const response = await fetch("/api/auth/openid", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ provider: "openid" }),
        credentials: "include",
      })
      if (!response.ok) {
        throw new Error(`SSO login failed (${response.status})`)
      }
      const payload = await response.json()
      setUser(payload?.user ?? null)
      return payload?.user ?? null
    } catch (error) {
      console.warn("SSO login request failed", error)
      setUser(null)
      throw error
    } finally {
      setIsAuthenticating(false)
    }
  }, [])

  const logout = React.useCallback(async () => {
    setIsAuthenticating(true)
    try {
      await fetch("/api/auth/openid", {
        method: "DELETE",
        credentials: "include",
      })
    } catch (error) {
      console.warn("Failed to clear auth session", error)
    } finally {
      setUser(null)
      setIsAuthenticating(false)
    }
  }, [])

  const value = React.useMemo(
    () => ({
      user,
      isLoading,
      isAuthenticating,
      login,
      logout,
      refresh: fetchSession,
    }),
    [user, isLoading, isAuthenticating, login, logout, fetchSession],
  )

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = React.useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}

export const AuthConsumer = AuthContext.Consumer

